<template>
    <div></div>
</template>

<script>
export default {
    name: "FlomotionComp",
    data: () => ({

    }),
    created () {
        window.addEventListener('scroll', this.handleScroll);
        window.addEventListener('resize', this.handleResize);
    },
    destroyed () {
        window.removeEventListener('scroll', this.handleScroll);
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
        handleScroll () {
            window.checkIfInView();
            this.$emit('set-actives-again')
        },
        handleResize () {
            this.$emit('on-set-again-flomotion-component')
        }
    },
}
</script>

